











































.alpheios-alignment-notification-bar {
    padding: 15px 15px 0;

    .alpheios-alignment-notification-bar-message {
        padding: 10px 30px 10px 10px;
        border-radius: 15px;
        margin-bottom: 10px;
        border: 1px solid transparent;
        position: relative;
        
        .alpheios-alignment-notification-bar__close-btn {
            padding: 0 10px 0 20px;
            cursor: pointer;

            display: inline-block;
            position: absolute;
            top: 30%;
            right: 0;

            & svg {
                width: 20px;
                height: 20px;
            }
        }

        &.alpheios-alignment-notification-bar-message___error {
            background:rgba(198, 73, 6, 0.31);
            color: #C64906;
            border-color: #C64906;

            .alpheios-alignment-notification-bar__close-btn {
                fill: #C64906;
                stroke: #C64906;

                &:hover,
                &:focus {
                    fill: #F27431;
                    stroke:  #F27431;
                }

                &:active {
                    fill:  #F27431;
                    stroke:  #F27431;
                }
            }
        }

        &.alpheios-alignment-notification-bar-message___info {
            background: #BCE5F0;
            color: #757575;
            border-color: #757575;

            .alpheios-alignment-notification-bar__close-btn {
                fill: #757575;
                stroke: #757575;

                &:hover,
                &:focus {
                    fill: #1B374F;
                    stroke:  #1B374F;
                }

                &:active {
                    fill:  #1B374F;
                    stroke:  #1B374F;
                }
            }
        }


    }
}
