












































.alpheios-alignment-options-block .alpheios-alignment-editor-container{
  border-bottom: 5px solid #f8f8f8;
  border-top: 5px solid #f8f8f8;
}
.alpheios-alignment-options__group {
  padding: 10px;
  border: 2px groove threedface;
  margin-bottom: 30px;

  legend {
    display: block;
    padding: 0 10px;
    margin-bottom: 10px;
    line-height: inherit;
    color: inherit;
    white-space: normal;
    font-size: 110%;
    font-weight: bold;
  }
}
