
























































































































































































































.alpheios-alignment-editor-text-block {
    textarea {
        width:100%;
        min-height: 300px;
    }

    p {
        margin-bottom: 10px;
    }
}

.alpheios-alignment-editor-text-block__title {
  position: relative;
}
.alpheios-alignment-editor-text-block__remove {
  display: inline-block;
  width: 25px;
  height: 25px;
  right: 0;
  top: 50%;
  top: 0;

  position: absolute;

  cursor: pointer;
  svg {
    display: inline-block;
    width: 100%;
    height: 100%;
  }
}

.alpheios-alignment-options-fieldset {
  padding: 10px;
  border: 2px groove #f8f8f8;
  margin-bottom: 30px;

  legend {
    display: block;
    padding: 0 10px;
    margin-bottom: 10px;
    line-height: inherit;
    color: inherit;
    white-space: normal;
    font-size: 110%;
  }
}

fieldset.alpheios-alignment-slim-fieldset {
  padding: 0;
  margin-bottom: 0;
  border: 0;

  legend {
    display: none;
  }
}

.alpheios-alignment-fieldset-label-auto {
  .alpheios-alignment-option-item__label {
    width: auto;
    margin-right: 20px;
  }
  input.alpheios-alignment-input, 
  .alpheios-alignment-select, 
  .alpheios-alignment-radio-block {
    width: auto;
  }
}
