.alpheios-alignment-option-item {
  display: inline-block;
  width: 100%;
  vertical-align: top;
  padding-right: 20px;
  margin-bottom: 20px;
}
.alpheios-alignment-option-item > label {
  width: 48%;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-option-item input.alpheios-alignment-input,
.alpheios-alignment-option-item .alpheios-alignment-select,
.alpheios-alignment-option-item .alpheios-alignment-radio-block {
  display: inline-block;
  width: 50%;
  vertical-align: top;
}
.alpheios-alignment-option-item p.alpheios-alignment-radio-block {
  margin-top: 0;
  margin-bottom: 0;
}
.alpheios-alignment-option-item .alpheios-alignment-select-input__select-container span,
.alpheios-alignment-option-item .alpheios-alignment-select-input__input-container span {
  min-width: 160px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-option-item .alpheios-alignment-select-input__select,
.alpheios-alignment-option-item .alpheios-alignment-select-input__input {
  width: 80%;
  max-width: 300px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-option-item .alpheios-alignment-select-input__input-description {
  font-size: 90%;
  color: #888;
}